.uxora-about{
    &-thumb{
        position: relative;
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
        img{
            border-radius: 20px;
        }
        &.thumb2{
            text-align: right;
            @include max (991px){
                text-align: left;
            }
        }
    }
    &-number{
        position: absolute;
        top: 0;
        z-index: 1;
        left: 48%;
        width: 60px;
        height: 60px;
        display: flex;
        align-items: center;
        border-radius: 100px;
        justify-content: center;
        transition: all 0.4s;
        background-color: var(--heading-color);
        h3{
            color: var(--white-color);
        }
        @include max (1399px){
            display: none;
        }
    }
    
    
}
.pl-128{
    padding-left: 128px;
    @include max (1399px){
        padding-left: 26px;
    }
    @include max (991px){
        padding-left: 0;
    }
}
.pr-128{
    padding-right: 128px;
    @include max (1399px){
        padding-right: 26px;
    }
    @include max (991px){
        padding-right: 0;
    }
}
.pb-80{
    padding-bottom: 80px;
    position: relative;
    transition: all 0.4s;
    @include max (991px){
        padding-bottom: 60px;
    }
    @include max (767px){
        padding-bottom: 40px;
    }
    &:hover{
        .border-r2{
            &::before{
                opacity: 1;
            }   
        }
        .uxora-about-number{
            background-color: var(--accent-color);
        }
    }
    .border-r2{
        &::before{
            top: 52%;
            left: 654px;
            transform: translate(-50%, -50%);
            width: 5px;
            height: 480px;
            content: "";
            position: absolute;
            opacity: 0;
            transition: all 0.4s;
            background-color: var(--accent-color);
            @include max (1399px){
                display: none;
            }
        }
    }
}
.pb-81{
    padding-bottom: 0;
    position: relative;
    transition: all 0.4s;
    &:hover{
        .border-r2{
            &::before{
                opacity: 1;
            }   
        }
        .uxora-about-number{
            background-color: var(--accent-color);
        }
    }
    .border-r{
        position: relative;
        &::before{
            top: 0;
            left: 50%;
            width: 5px;
            height: 400px;
            content: "";
            position: absolute;
            background-color: #181B0E1A;
            @include max (1399px){
                display: none;
            }
        }
    }
    .border-r2{
        &::before{
            top: 52%;
            left: 654px;
            transform: translate(-50%, -50%);
            width: 5px;
            height: 382px;
            content: "";
            position: absolute;
            opacity: 0;
            transition: all 0.4s;
            background-color: var(--accent-color);
            @include max (1399px){
                display: none;
            }
        }
    }
}
.border-r{
    position: relative;
    &::before{
        top: 0;
        left: 50%;
        width: 5px;
        height: 480px;
        content: "";
        position: absolute;
        background-color: #181B0E1A;
        @include max (1399px){
            display: none;
        }
    }
}

@keyframes updown {
	0% {
		transform: translatey(0px);
	}
	50% {
		transform: translatey(-30px);
	}
	100% {
		transform: translatey(0px);
	}
}

