
.uxora-social{
    &-wrap{
        ul{
            li{
                display: flex;
                align-items: center;
                gap: 15px;
                transition: all 0.4s;
                font-size: 21px;
                line-height: 28px;
                &:hover{
                    a {
                        color: var(--accent-color);
                        &.wrap{
                            border: 1px solid transparent;
                            background-color: var(--accent-color);
                        }
                        path{
                            fill: var(--white-bg);
                        }
                    }
                }
                a{
                    &.wrap{
                        width: 35px;
                        height: 35px;
                        display: flex;
                        align-items: center;
                        border-radius: 100px;
                        justify-content: center;
                        transition: all 0.4s;
                        border: 1px solid var(--heading-color);
                    }
                    &.wrap2{
                        position: relative;
                        &::before{
                            content: "";
                            position: absolute;
                            bottom: 0;
                            left: auto;
                            right: 0;
                            height: 1px;
                            width: 0;
                            background-color: var(--accent-color);
                            transition: all .3s ease;
                        }
                        &:hover::before{
                            left: 0;
                            right: auto;
                            width: 100%;
                        }
                    }
                }
                
            }
        }
    }
    &-wrap2{
        width: 100%;
        height: 88px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: linear-gradient(135deg, #FCD569 0%, #0A23F4 100%);
        ul{
            li{
                display: inline-block;
                margin-right: 14.8px;
                transition: all 0.4s;
                &:last-child {
                    margin-right: 0;
                }
                a{
                    width: 40px;
                    height: 40px;
                    display: flex;
                    align-items: center;
                    border-radius: 100px;
                    justify-content: center;
                    transition: all 0.4s;
                    background-color: var(--light-color2);
                    border: 1px solid transparent;
                    &:hover{
                        border: 1px solid transparent;
                        background-color: var(--accent-color);
                        path{
                            fill: var(--white-bg);
                        }
                    }
                }
            }
        }
    }
    &-wrap3{
        display: flex;
        align-items: center;
        gap: 24px;
        ul{
            li{
                display: inline-block;
                margin-right: 14.8px;
                transition: all 0.4s;
                &:last-child {
                    margin-right: 0;
                }
                a{
                    width: 40px;
                    height: 40px;
                    display: flex;
                    align-items: center;
                    border-radius: 100px;
                    justify-content: center;
                    transition: all 0.4s;
                    background-color: var(--light-color2);
                    border: 1px solid transparent;
                    &:hover{
                        border: 1px solid transparent;
                        background-color: var(--accent-color);
                        path{
                            fill: var(--white-bg);
                        }
                    }
                }
            }
        }
        @include max (1399px){
            margin-top: 24px;
        }
        @include max (991px){
            margin-top: 0;
        }
        @include max (767px){
            margin-top: 24px;
        }
    }
}
