.uxora-hero{
    &-shape {
        position: absolute;
        top: 40px;
        right: -20%;
        z-index: -1;
        @include max (991px){
            left: 100px;
        }
        @include max (1800px){
            display: none;
        }
    }
    &-shape2 {
        position: absolute;
        bottom: -30px;
        left: -30%;
        z-index: -1;
        @include max (1800px){
            display: none;
        }
    }
 
}

// end hero

.uxora-about{
    &-shape1 {
        position: absolute;
        top: 18%;
        right: -60%;
        @include max (1199px){
            display: none;
        }
    }
    &-shape2 {
        position: absolute;
        top: -55%;
        left: -50%;
        @include max (1800px){
            display: none;
        }
    }
}
// add service


.uxora-service-shape {
    position: absolute;
    bottom: 100px;
    right: -35%;
    @include max (1199px){
        display: none;
    }
}
.uxora-process-shape {
    position: absolute;
    top: 0px;
    right: 50px;
    @include max (767px){
        display: none;
    }
}
.uxora-t-shape {
    position: absolute;
    top: 0;
    right: -90%;
    @include max (1399px){
        right: -64%;
    }
    @include max (1199px){
        right: -37%;
    }
    @include max (991px){
        display: none;
    }
}
.uxora-breadcrumbs-shape {
    position: absolute;
    bottom: 0;
    right: -25%;
    @include max (1399px){
        right: -15%;
    }
    @include max (1199px){
        right: 0;
    }
    @include max (991px){
        display: none;
    }
}

.faq-shape {
    position: absolute;
    top: 230px;
    right: 50px;
    @include max (991px){
        display: none;
    }
}
.uxora-cta{
    &-shape1{
        position: absolute;
        top: 0;
        left: 0;
        @include max (991px){
            display: none;
        }
        img{
            border-radius: 10px 0 0 0;
        }
    }
    &-shape2{
        position: absolute;
        top: 0px;
        left: 2px;
        @include max (991px){
            display: none;
        }
    }
    &-shape3{
        position: absolute;
        bottom: -40px;
        right: -30%;
        @include max (1450px){
            display: none;
        }
    }
}
