.uxora-pricing{
    &-wrap{
        padding: 30px;
        border-radius: 20px;
        transition: all 0.4s;
        margin-bottom: 24px;
        background-color: var(--light-color2);
        @include max (991px){
            padding: 30px 20px;
        }
    }
    &-header{
        padding-bottom: 24px;
       
    }
    &-price{
        gap: 5px;
        display: flex;
        align-items: flex-end;
        padding-bottom: 24px;
    }
    &-body{
        p{
            padding-bottom: 24px;
            border-bottom: 1px solid #181B0E1A;
            margin-bottom: 24px;
        }
        ul{
            li{
                position: relative;
                padding-bottom: 15px;
                padding-left: 40px;
                font-size: 21px;
                font-weight: 600;
                line-height: 30px;
                &:last-child{
                    padding-bottom: 0;
                }
                img{
                    position: absolute;
                    left: 0;
                    top: 3px;
                }
            }
        }
    }
    &-footer{
      margin-top: 30px;
    }
    // end 
}

.ux-price-value.dynamic-value {
    font-family: $heading-font;
    font-size: 80px;
    font-weight: 700;
    line-height: 90px;
    letter-spacing: -1px;
}



.toggle-switch {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}
.toggle-switch input {
    display: none;
}
.toggle-switch,
.toggle-switch span {
}
.toggle-switch input:checked + span + span,
.toggle-switch input + span {
    padding: 13px 30px;
    background-color: var(--body-color);
}
.toggle-switch input + span + span,
.toggle-switch input:checked + span {
    background-color: transparent;
    border-color: transparent;
    padding: 16px 28px;
    color: var(--heading-color);
  }
// end switcher btn

// add table
    

.mt-40{
    margin-top: 40px;
}

.pricing-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 50px;
    @include max (991px){
        margin-bottom: 40px;
    }
    @include max (767px){
        margin-bottom: 30px;
        display: block;
        text-align: center;
    }
    label {
        margin: 0 20px;
        color: var(--heading-color);
        font-weight: 700;
        @include max(767px) {
        margin: 0 10px;
        font-size: 16px;
        }
    }
    &.dark{
        label{
            color: var(--heading-color);
        }
    }
}
.toggle-btn{
    @include max (767px){
        margin: 10px 0;
    }
}

[data-pricing-dynamic][data-value-active="yearly"] .dynamic-value:before {
  display: inline-block;
  content: attr(data-yearly);
  }
  [data-pricing-dynamic][data-value-active="yearly"] [data-pricing-trigger] {
  background: #ff5722;
  }
  
  [data-pricing-dynamic][data-value-active="yearly"] [data-pricing-trigger] span {
  left: calc(100% - 33px);
  }
  [data-pricing-dynamic][data-value-active="monthly"] .dynamic-value:before {
  display: inline-block;
  content: attr(data-monthly);
  }
  .dynamic-value:before {
    display: inline-block;
    content: attr(data-active);
  }
  .static-value:before {
    display: inline-block;
    content: attr(data-active);
  }
  .pricing-btn .btn-toggle {
    position: relative;
    width: 70px !important;
    height: 35px;
    border-radius: 50px;
    border: none;
    margin: 0;
    padding: 0!important;
    cursor: pointer;
    background-size: 25px;
    border: none!important;
    background-position: center right 5px;
    border: none !important;
    background-position: center right 5px;
    background-color: var(--accent-color);
    background-image: url(../images/pricing/switcher-shape.svg) !important;
}
 
.form-check-input:focus {
    border-color: none;
    outline: 0;
    box-shadow:none;
}
.pricing-btn .btn-toggle:checked {
    background-position: center left 5px;
    background-image: url(../images/pricing/switcher-shape.svg) !important;
}



