.uxora-service{
  &-table-item{
    display: flex;
    align-items: center;
    padding-bottom: 50px;
    border-bottom: 1px solid #181B0E1A;
    margin-bottom: 50px;
    @include max (991px){
        padding-bottom: 30px;
        border-bottom: 1px solid #181B0E1A;
        margin-bottom: 30px;
    }
    &:last-child{
        padding-bottom: 0;
        border-bottom: none;
        margin-bottom: 0;
    }
    @include max (991px){
        display: block;
    }
  }
  &-table-title{
    flex: 0 0 36%;
    h4{
        padding-bottom: 12px;
    }
  }
  &-table-body{
    flex: 0 0 44%;
    padding: 0 0 0 80px;
    @include max (1399px){
        padding: 0;
    }
    @include max (991px){
        padding: 15px 0;
    }
  }
  &-table-btn{
    flex: 0 0 20%;
    text-align: right;
    @include max (991px){
        text-align: left;
    }
  }
  &-d-wrap{
    max-width: 850px;
    margin: 0 auto;
  }
  &-d-thumb{
    img{
        border-radius: 20px;
        margin-bottom: 30px;
    }
    p{
        margin-bottom: 30px;
    }
  }
  &-d-content{
    h3{
        padding-bottom: 20px;
    }
    ul{
        li{
            position: relative;
            padding-left: 20px;
            padding-bottom: 20px;
            &:last-child{
                padding-bottom: 0;
            }
            span{
                font-weight: 600;
            }
            &::before{
                content: "";
                position: absolute;
                top: 10px;
                left: 0;
                width: 6px;
                height: 6px;
                border-radius: 100px;
                background-color: var(--heading-color);
            }
        }
    }
    &.content2{
        ul{
            li{
                padding-bottom: 5px;
                &:last-child{
                    padding-bottom: 0;
                }
            }
        }
    }
    .data{
        margin-bottom: 5px;
        font-weight: 600;
    }
  }
  &-d-thumb2{
    position: relative;
    overflow: hidden;
    border-radius: 20px;
    margin: 30px 0;
    img{
        width: 100%;
        height: 100%;
        transition: all 0.4s;
        border-radius: 20px;
        &:hover{
            transform: scale(1.1);
        }
    }
    &.thumb3{
        @include max (767px){
            margin-top: 0;
        }
    }
  }
}
// add process

.uxora-process{
    &-wrap{
        position: relative;
        border-radius: 10px;
        transition: all 0.4s;
        &.pt-30{
            @include max (991px){
                padding-top: 40px;
            }
        }
        &.pt-32{
            @include max (767px){
                padding-top: 40px;
            }
        }
    }
    &-number{
        width: 60px;
        height: 60px;
        display: flex;
        align-items: center;
        border-radius: 100px;
        justify-content: center;
        background-color: var(--heading-color);
        h3{
            color: var(--light-color);
        }
    }
    &-content{
        h3{
            padding: 30px 0 15px 0;
            @include max (991px){
                padding: 24px 0 15px 0;
            }
        }
    }
}
.pt-31{
    @include max (1199px){
        padding-top: 30px;
    }
}

.pt-30{
    padding-top: 30px;
}

