.uxora-cta{
    &-section{
        padding: 130px 0;
        background-color: var(--accent-color);
        position: relative;
        border-radius: 20px;
        margin: 0 auto;
        z-index: 1;
        width: calc(100% - 200px);
        @include max (1399px){
            width: calc(100% - 150px);
        }
        @include max (1199px){
            width: calc(100% - 100px);
        }
        @include max (991px){
            padding: 80px 0;
            width: calc(100% - 70px);
        }
        @include max (767px){
            width: calc(100% - 20px);
            padding: 60px 0;
        }
    }
    &-title{
        max-width: 765px;
        text-align: center;
        margin: 0 auto;
        position: relative;
        @include max(991px) {
            padding: 0 40px;
        }
        @include max(575px) {
            padding: 0 20px;
        }
        h2{
            color: var(--white-color);
        }
    }
    &-btn{
        padding-top: 50px;
        display: flex;
        gap: 24px;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        @include max (991px){
            padding-top: 40px;
        }
        @include max (767px){
            padding-top: 30px;
        }
    }
    // end
}

