.uxora-p{
   &-wrap{
    transition: all 0.4s;
    position: relative;
    &:hover{
        .uxora-p-content{
            opacity: 1;
        }
        .uxora-p-thumb::before{
            opacity: 1;
        }
        .uxora-p-btn{
            opacity: 1;
        }
    }
   }
    &-thumb{
        position: relative;
        display: block;
        text-align: center;
        overflow: hidden;
        border-radius: 10px;
        white-space: nowrap;
        margin-bottom: 24px;
        margin-left: 24px;
        z-index: 0;
        &:hover img{
            transform: scale(1.1);
        }
        img{
            border-radius: 10px;
            transition: all 0.4s;
            object-fit: cover;
            width: 100%;
            height: 100%;
            
        }
        &::before{
            position: absolute;
            content: "";
            width: 100%;
            opacity: 0;
            height: 100%;
            top: 0;
            mix-blend-mode: multiply;
            left: 0;
            z-index: 1;
            transition: .4s;
            background: linear-gradient(135deg, #FCD569 0%, #0A23F4 100%);
        }
    }
    &-content {
        bottom: 30px;
        left: 30px;
        opacity: 0;
        z-index: 1;
        transition: all .4s;
        text-align: left;
        position: absolute;
        max-width: 405px;
        h3{
            padding-bottom: 15px;
            color: var(--white-color);
        }
        p{
            color: var(--white-color);
            white-space: wrap;
        }
        @include max (991px){
            bottom: 15px;
            left: 15px;
        }
    }
    &-btn{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0;
        transition: all 0.4s;
        z-index: 2;
        @include max (1600px){
            top: 40%;
        }
        @include max (1399px){
            top: 50%;
        }
        @include max (1199px){
            top: 35%;
        }
        @include max (767px){
            top: 50%;
        }
        @include max (500px){
            top: 17%;
        }
    }
    // add single
   
    &-d-thumb{
        
        img{
            border-radius: 10px;
            padding-bottom: 30px;
        }
    } &-d-data-wrap{
        margin-bottom: 50px;
        @include max (991px){
            margin-bottom: 40px;
        }
        @include max (767px){
            margin-bottom: 30px;
        }
        ul{
            display: flex;
            align-items: center;
            justify-content: space-between;
            @include max (767px){
                display: block;
            }
            li{
                display: inline-block;
                @include max (767px){
                    padding-bottom: 10px;
                    &:last-child{
                        padding-bottom: 0;
                    }
                }
                span{
                    font-weight: 600;
                }
            }
        }
    }
}
.uxora-p-slider-init2.slick-initialized.slick-slider {
    direction: rtl;
}
.uxora-portfolio{
    &-menu{
        margin-bottom: 50px;
        @include max (991px){
            margin-bottom: 40px;
        }
        @include max (767px){
            margin-bottom: 30px;
        }
        ul{
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 24px;
            flex-wrap: wrap;
            li{
                border: 2px solid var(--heading-color);
                &.active{
                    background: var(--accent-color);
                    color: var(--white-color);
                    border: 2px solid var(--accent-color);
                }
            }
        }
    }
}






.uxora-portfolio-column{
    margin-left: -24px;
}

ul#watch-filter-gallery li{
    cursor: pointer;
}