.uxora-hero{
  &-section{
    padding: 190px 0 378px;
    position: relative;
    z-index: 0;
    @include max (1199px){
      padding: 190px 0 330px;
    }
    @include max (991px){
      padding: 190px 0 210px;
    }
    @include max (767px){
      padding: 130px 0 100px;
    }
  }
  &-content{
    position: relative;
    max-width: 982px;
    margin: 0 auto;
    text-align: center;
    padding-bottom: 80px;
    p{
      font-size: 21px;
      line-height: 30px;
    }
    @include max (1199px){
      max-width: 800px;
    }
    @include max (991px){
      padding-bottom: 60px;
    }
    @include max (767px){
      padding-bottom: 40px;
    }
  }
  &-thumb{
    text-align: center;
    position: relative;
    margin-top: -378px;
    @include max (1199px){
      margin-top: -330px;
    }
    @include max (991px){
      margin-top: -210px;
    }
    @include max (767px){
      margin-top: -100px;
    }
  }
  // end one
  
}
