.uxora-contact{
    &-box{
       p{
          margin-top: 24px;
          margin-bottom: 10px;
          font-size: 18px;
          font-weight: 700;
          line-height: 30px;
       }
       &.box2{
            padding: 30px;
            border-radius: 20px;
            background-color: var(--light-color2);
            margin-left: 50px;
            @include max (991px){
                margin-top: 40px;
                margin-left: 0;
            }
            @include max (767px){
                margin-top: 30px;
                padding: 30px 20px;
            }
            .text{
                font-size: 21px;
                line-height: 30px;
                font-weight: 400;
                margin: 0;
            }
        }
    }
    &-info-wrap{
        display: flex;
        gap: 24px;
        margin-bottom: 50px;
        border-bottom: 1px solid #181B0E1A;
        padding-bottom: 50px;
        &:last-child{
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 0;
        }
        @include max (991px){
            margin-bottom: 40px;
            padding-bottom: 40px;
        }
        @include max (767px){
            margin-bottom: 30px;
            padding-bottom: 30px;
        }
    }
    &-info-icon{
        width: 60px;
        height: 60px;
        display: flex;
        align-items: center;
        border-radius: 100px;
        justify-content: center;
        background-color: var(--accent-color);
    }
    &-info-content{
        h3{
            margin-bottom: 15px;
        }
        ul{
            li{
                a{
                    display: block;
                    font-size: 21px;
                    font-weight: 400;
                    line-height: 30px;
                    color: var(--heading-color);
                }
            }
        }
    }





    
    &-us-map {
        width: 100%;
        height: 750px;
        @include max (991px){
            height: 400px;
        }
    }
}