.uxora-faq{
  &-item{
    margin-bottom: 24px;
    background-color: var(--light-color2);
    padding: 30px;
    border-radius: 20px;
    position: sticky;
    transition: all 0.4;
    &.open {
      .uxora-faq-body{
          display: block;
        }
        .uxora-active-icon{
          transform: rotate(180deg);
        }
    }
  }
  &-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  &-body{
    display: none;
    padding-right: 45px;
    padding-top: 10px;
  }
}


.uxora-active-icon{
  flex-shrink: 0;
  transition: all 0.4s;
  img{
    @include max (767px){
      width: 40px;
    }
  }
}

.tabs{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 24px;
  flex-wrap: wrap;
  padding-bottom: 50px;
  @include max (991px){
    margin-bottom: 40px;
  }
  @include max (767px){
    margin-bottom: 30px;
  }
  li{
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    padding: 18px 24px;
    border-radius: 50px;
    cursor: pointer;
    transition: all 0.4s;
    border: 2px solid var(--heading-color);
    &.current{
      border: 2px solid var(--accent-color);
      color: var(--light-color);
      background-color: var(--accent-color);
    }
  }
}


.uxora-tab-content{
	display: none;

}

.uxora-tab-content.current{
	display: inherit;
}