.uxora-blog{
    &-wrap{
        padding: 30px;
        border-radius: 20px;
        margin-bottom: 24px;
        transition: all 0.4s;
        background-color: var(--light-color2);
        &:hover{
            .uxora-blog-thumb img{
                transform: scale(1.1);
            }
            
        }
        &.wrap2{
            margin-right: 50px;
            @include max (991px){
                margin-right: 0;
            }
        }
    }
    &-thumb{
        border-radius: 15px;
        margin-bottom: 24px;
        position: relative;
        overflow: hidden;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
        }
    }
    &-meta{
        padding-bottom: 24px;
        ul{
            display: flex;
            flex-wrap: wrap;
            column-gap: 24px;
            row-gap: 15px;
            li{
                position: relative;
                padding-left: 30px;
                transition: all 0.4s;
                @include max (767px){
                    margin-bottom: 5px;
                }
                &:last-child{
                    margin-right: 0;
                    margin-bottom: 0;
                }
                
                a{
                    font-size: 18px;
                    line-height: 25px;
                    transition: all 0.4s;
                    color: var(--heading-color);
                    &:hover{
                        color: var(--accent-color);
                        svg{
                            stroke: var(--accent-color);
                        }
                    }
                    svg{
                        position: absolute;
                        top: 4px;
                        transition: all 0.4s;
                        left: 0;
                    }
                }
            }
        }
    }
    &-content{
        padding-bottom: 30px;
        a{
            color: var(--heading-color);
        }
    }
    // strat inner page
    &-widgets {
        margin-bottom: 24px;
        background-color: var(--light-color2);
        padding: 30px;
        border-radius: 20px;
        &:first-child{
            padding: 0;
            background-color: transparent;
        }
        &:last-child{
            margin-bottom: 0;
        }
        h3{
            padding-bottom: 24px;
        }
        @include max (991px){
            margin-bottom: 40px;
        }
        &.widgets2{
            h3{
                padding-bottom: 12px;
            }
        }
        &.widgets3{
            display: flex;
            padding: 0;
            margin: 0;
            background-color: transparent;
            gap: 24px;
            align-items: center;
            flex-wrap: wrap;
            h3{
                padding-bottom: 0;
            }
        }
    }
    &-categorie{
       ul{
        li{
            margin-bottom: 15px;
            &:last-child{
                margin-bottom: 0;
            }
            a{
                font-size: 21px;
                line-height: 30px;
                transition: all 0.4s;
                color: var(--heading-color);
                &:hover{
                    color: var(--accent-color);
                }
            }
        }
       }
    }
    &-recent-post-item{
        display: flex;
        align-items: center;
        gap: 15px;
        margin-bottom: 24px;
        @include max(1399px) {
            display: block;
        }
        @include max(991px) {
            display: flex;
        }
        @include max(767px) {
            display: block;
        }
        &:last-child{
            margin-bottom: 0;
        }
        &:hover{
            .uxora-blog-recent-post-thumb img{
                transform: scale(1.1);
            }
            .uxora-blog-recent-post-data{
                h5{
                    background-size: 100% 1px;
                }
            }
        }
    }
    &-recent-post-thumb {
        border-radius: 10px;
        overflow: hidden;
        flex-shrink: 0;
        transition: all 0.4s;
        @include max(1399px) {
            margin-bottom: 15px;
        }
        @include max(991px) {
            margin-bottom: 0;
        }
        @include max(767px) {
            margin-bottom: 15px;
        }
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
        }
    }
    &-recent-post-data{
        ul {
            li{
                font-size: 18px;
                line-height: 25px;
                color: var(--heading-color);
                margin-bottom: 10px;
                position: relative;
                padding-left: 30px;
                transition: all 0.4s;
                &:hover{
                    color: var(--accent-color);
                    path{
                        fill: var(--accent-color);
                    }
                }
                svg{
                    position: absolute;
                    top: 3px;
                    left: 0;
                    transition: all 0.4s;
                    margin-right: 10px;
                }
            }
        }
        h5{
            margin-top: 5px;
            background-image: linear-gradient(currentColor, currentColor);
            background-position: 0 100%;
            background-repeat: no-repeat;
            background-size: 0 1px;
            transition: background-size .3s ease-in-out;
            display: inline;
            padding-bottom: 0;
            font-size: 20px;
            line-height: 28px;
        }
    }
    &-tags{
        ul{
            display: flex;
            gap: 10px;
            flex-wrap: wrap;
            li{
                display: inline-block;
                a{
                    font-size: 16px;
                    line-height: 24px;
                    padding: 8px 22px;
                    display: block;
                    border-radius: 50px;
                    transition: all .4s;
                    color: var(--heading-color);
                    background-color: var(--light-color);
                    &:hover{
                        color: var(--white-color);
                        background-color: var(--accent-color);
                    }
                }
            }
        }
        &.tags2{
            ul{
              
                li{
                    a{
                        
                        background-color: var(--light-color2);
                        &:hover{
                            color: var(--white-color);
                            background-color: var(--accent-color);
                        }
                    }
                }
            }
        }
    }
    // single page
    &-d-wrap{
        padding-right: 52px;
        @include max (1199px){
            padding-right: 0;
        }
    }
    &-d-thumb{
        padding-bottom: 24px;
        img{
            border-radius: 20px;
        }
    }
    &-d-content{
        padding-bottom: 30px;
        h3{
            padding-bottom: 20px;
        }
        ul{
            li{
                position: relative;
                margin-bottom: 15px;
                padding-left: 20px;
                &::before{
                    content: "";
                    position: absolute;
                    top: 10px;
                    left: 0;
                    width: 8px;
                    height: 8px;
                    border-radius: 100px;
                    background-color: var(--heading-color);
                }
                &:last-child{
                    margin-bottom: 0;
                }
            }
        }
    }
    &-d-content-wrap{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 40px 0;
        border-top: 1px solid #142D6F1A;
        border-bottom: 1px solid #181B0E1A;
        margin: 40px 0;
        flex-wrap: wrap;
        @include max (767px){
            margin: 30px 0;
            display: block;
        }
    }
    &-d-comment-box{
        h3{
            padding-bottom: 30px;
        }
    }
    &-d-comment-wrap1{
        display: flex;
        .reply-btn a{
            font-weight: 500;
            padding-top: 10px;
            transition: all 0.4s;
            color: var(--heading-color);
            &:hover{
                color: var(--accent-color);
            }
        }
        &.wrap2{
            padding-bottom: 24px;
            border-bottom: 1px solid #142D6F1A;
            margin-bottom: 24px;
        }
    }
    &-d-comment-wrap{
        display: flex;
        padding-top: 24px;
        border-top: 1px solid #142D6F1A;
        margin-top: 24px;
        margin-left: 200px;
        @include max (991px){
            margin-left: 90px;
        }
        @include max (767px){
            margin-left: 0px;
        }
        .reply-btn a{
            font-size: 16px;
            line-height: 24px;
            font-weight: 700;
            padding-top: 10px;
            transition: all 0.4s;
            color: var(--heading-color);
            &:hover{
                color: var(--accent-color);
            }
        }
    }
    &-d-comment-thumb{
        margin-right: 20px;
        flex-shrink: 0;
        img{
            border-radius: 100px;
        }
    }
    &-d-comment-data1{
        h6{
            font-family: $body-font;
            font-size: 16px;
            font-weight: 700;
            line-height: 24px;
        }
        span{
            font-size: 14px;
            line-height: 20px;
        }
        p{
            padding-top: 15px;
        }
    }
    &-d-comment-data{
        span{
            font-size: 16px;
            line-height: 22px;
        }
        p{
            color: var(--dark-color1);
            padding-top: 10px;
        }
    }
    &-d-comment-box2{
        margin-top: 40px;
        border-top: 1px solid #142D6F1A;
        padding-top: 40px;
        h3{
            padding-bottom: 15px;
        }
    }
    &-d-content-comment-box{
        h4{
            margin-bottom: 30px;
        }
    }
}

.uxora-search-box {
    position: relative;
    padding: 0 60px 0 0;
    border-radius: 50px;
    background-color: var(--light-color2);
    input{
        font-size: 16px;
        line-height: 24px;
        padding: 12px 0 12px 24px!important;
    }
}
button#uxora-search-btn {
    position: absolute;
    right: 5px;
    top: 5px;
    width: 50px;
    height: 50px;
    border-radius: 100px;
    transition: all 0.4s;
    background-color: var(--accent-color);
    .ri-search-line{
        color: var(--white-color);
    }
    &:hover{
        background-color: var(--heading-color);
    }
}
blockquote {
    margin-top: 30px;
    border-radius: 30px;
    padding: 30px 100px 30px 30px;
    background-color: var(--light-color2);
    @include max (767px){
        padding: 30px;
    }
    p{
        font-size: 21px;
        font-weight: 600;
        line-height: 30px;
    }
    span{
        font-size: 21px;
        line-height: 30px;
    }
}
.uxora-blog-d-thumb {
    img{
        width: 100%;
    }
}
.mt-41{
    @include max (991px){
        margin-top: 40px;
    }
    @include max (767px){
        margin-top: 40px;
    }
}